<template>
  <div>
    <h3>查看历史版本</h3>
    <p class="version-num">一共 <i>4</i> 个版本</p>
    <el-table
      :data="tableData"
      border
      class="version-table">
        <el-table-column
          label="版本"
          width="180">
          <template scope="scope">
            <el-tag type="gray">{{ scope.row.version }}</el-tag>
          </template>
        </el-table-column>
        <el-table-column
          label="姓名"
          width="180">
          <template scope="scope">
              <p> {{ scope.row.name }}</p>
          </template>
        </el-table-column>
        <el-table-column label="创建日期">
          <template scope="scope">
            <p> {{ scope.row.date }}</p>
          </template>
        </el-table-column>
        <el-table-column label="完善度">
          <template scope="scope">
            <p> {{ scope.row.finished }}</p>
          </template>
        </el-table-column>
        <el-table-column label="操作">
          <template scope="scope">
            <el-button
              size="small"
              type="primary"
              >查看</el-button>

          </template>
        </el-table-column>
    </el-table>
  </div>
</template>

<script>

export default {
	data: function () {
		return {
      tableData: [{
          version: '0.0.1',
          date: '2016-05-02',
          name: '王小虎',
          finished:'86%',
          address: '上海市普陀区金沙江路 1518 弄'
        }, {
          version: '0.0.2',
          date: '2016-05-04',
          name: '王小虎',
          finished:'86%',
          address: '上海市普陀区金沙江路 1517 弄'
        }, {
          version: '0.0.3',
          date: '2016-05-01',
          name: '王小虎',
          finished:'86%',
          address: '上海市普陀区金沙江路 1519 弄'
        }, {
          version: '0.0.4',
          date: '2016-05-03',
          name: '王小虎',
          finished:'86%',
          address: '上海市普陀区金沙江路 1516 弄'
        }],
		}
	},
	methods: {

	},
	components: {

	}
}
</script>
<style lang="less">
.version-num{
  font-size:14px;
  margin-top:20px;
  i{
    color:#20A0FF;
  }
}
.version-table{
  margin-top:20px;
}
</style>



